<template>
  <header class="app-header">
    <img src="@/assets/image.png" alt="Header Image" class="header-image" @click="goToHome" />
    <nav class="nav-bar">
      <ul>
        <template v-if="isLoggedIn">
          <li>
            <a href="/mypage" @click.prevent="goToMyPage"></a>
          </li>
          <li>
            <a href="#" @click.prevent="logout"></a>
          </li>
        </template>
        <li v-else>
          <a href="/login" @click.prevent="navigateToLogin"></a>
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';

export default {
  name: "AppHeader",
  setup() {
    const isLoggedIn = ref(false);
    const router = useRouter();

    onMounted(() => {
      isLoggedIn.value = !!localStorage.getItem('jwtToken');
    });

    function goToHome() {
      router.push({ path: '/' });
    }

    function goToMyPage() {
      router.push('/mypage');
    }

    function navigateToLogin() {
      router.push('/login');
    }

    function logout() {
      localStorage.removeItem('jwtToken');
      localStorage.removeItem('memberId');
      localStorage.removeItem('memberNickname');
      isLoggedIn.value = false;
      router.push('/login');
    }

    return {
      isLoggedIn,
      goToHome,
      goToMyPage,
      logout,
      navigateToLogin,
    };
  },
};
</script>

