<template>
  <div class="container">
    <AppHeader />
    <div class="quiz-form">
      <h2> </h2>
      <form @submit.prevent="submitForm">
        <div class="form-group">
          <label for="category">:</label>
          <select id="category" v-model="quiz.quizCategory" required>
            <option disabled value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=" "> </option>
            <option value="  ">  </option>
            <option value=""></option>
            <option value=" "> </option>
            <option value=" "> </option>
            <option value=" "> </option>
            <option value=""></option>
            <option value=" "> </option>
            <option value=""></option>
            <option value=" "> </option>
            <option value="AI Quiz"> </option>
            <option value="AI Quiz"> </option>
          </select>
        </div>
        <div class="form-group">
          <label for="quiz"> :</label>
          <input 
            type="text" 
            id="quiz" 
            v-model="quiz.quizTitle" 
            required 
          />
        </div>
        <div class="form-group">
          <label for="type">:</label>
          <input type="text" id="type" v-model="quiz.quizType" value="O/X" readonly />
        </div>
        <div class="form-group">
          <label for="level">:</label>
          <select id="level" v-model="quiz.quizLevel" required>
            <option value="easy">Easy</option>
            <option value="normal">Normal</option>
            <option value="hard">Hard</option>
          </select>
        </div>
        <div class="form-group">
          <label for="answer">:</label>
          <select id="answer" v-model="quiz.quizAnswer" required>
            <option disabled value=""> </option>
            <option value="O">O</option>
            <option value="X">X</option>
          </select>
        </div>
        <div class="form-group">
          <label for="description">:</label>
          <div class="input-wrapper">
            <textarea 
              id="description" 
              v-model="quiz.quizDescription" 
              required
              maxlength="500"
            ></textarea>
            <span class="char-count">{{ quiz.quizDescription.length }}/500</span>
          </div>
        </div>
        <div class="button-container">
          <button type="submit"> </button>
        </div>
      </form>
    </div>
    <AppFooter />
  </div>
</template>

<script>
import AppHeader from "./Header.vue";
import AppFooter from "./Footer.vue";
import axios from "axios";

export default {
  components: {
    AppHeader,
    AppFooter,
  },
  data() {
    return {
      quiz: {
        memberId: localStorage.getItem("memberId"), 
        quizCategory: "",
        quizTitle: "",
        quizType: "O / X",
        quizAnswer: "",
        quizDescription: "",
        quizLevel: "normal" //  
      },
    };
  },
  methods: {
    async submitForm() {
  try {
    const token = localStorage.getItem("jwtToken"); // JWT  
    if (!token) {
      alert(" ."); //   
      this.$router.push("/login");
      return;
    }
    const beUrl = process.env.VUE_APP_BE_API_URL;
    await axios.post( beUrl +"/api/v1/quizs", this.quiz, {
      headers: {
        Authorization: `Bearer ${token}`
      },
      withCredentials: true
    });

    //      
    this.$router.push("/quizboard");
  } catch (error) {
    console.error("Failed to create quiz:", error);
    alert("  .   .");
  }
}

  },
};
</script>

